import { DATA_SUCCESS, DATA_FAIL, SET_MESSAGE } from "./../types";

import homeService from "../../services/website/Home.service";

export const Notifications = () => (dispatch) => {
  // console.log("action notification")
  return homeService.Notifications().then(
    (response) => {
      dispatch({
        type: DATA_SUCCESS,
      });

      dispatch({
        type: SET_MESSAGE,
        payload: response.data.message,
      });

      return Promise.resolve(response);
    },
    (error) => {
      const message =
        (error.response &&
          error.response.data &&
          error.response.data.message) ||
        error.message ||
        error.toString();

      dispatch({
        type: DATA_FAIL,
      });

      dispatch({
        type: SET_MESSAGE,
        payload: message,
      });

      return Promise.reject();
    }
  );
};
