export const GoLiveProduct =
  (
    title,
    name,
    description,
    keywords,
    category_id,
    sub_category_id,
    brand_id,
    model_id,
    country_id,
    city_id,
    start_date_time,
    end_date_time,
    auction_type,
    starting_price,
    high_price,
    final_price,
    refund,
    refund_days,
    youtube_link,
    product_img,
    video,
    start_time,
    end_time,
    year_id,
    custId
  ) =>
  (dispatch) => {
    return homeService
      .GoLiveProduct(
        title,
        name,
        description,
        keywords,
        category_id,
        sub_category_id,
        brand_id,
        model_id,
        country_id,
        city_id,
        start_date_time,
        end_date_time,
        auction_type,
        starting_price,
        high_price,
        final_price,
        refund,
        refund_days,
        youtube_link,
        product_img,
        video,
        start_time,
        end_time,
        year_id,
        custId
      )
      .then(
        (response) => {
          dispatch({
            type: DATA_SUCCESS,
          });

          dispatch({
            type: SET_MESSAGE,
            payload: response.data.message,
          });

          return Promise.resolve(response);
        },
        (error) => {
          const message =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();

          dispatch({
            type: DATA_FAIL,
          });

          dispatch({
            type: SET_MESSAGE,
            payload: message,
          });

          return Promise.reject();
        }
      );
  };
